import React from "react";

function AlertComponent({
  onNoPress,
  okayPress,
  title,
  content,
  okText = "Yes",
  noText = "No",
}) {
  return (
    <div className="content">
      {title != null && <h3>{title}</h3>}
      {content != null && <p>{content}</p>}
      <div className="footer">
        {okayPress != null && <button onClick={okayPress}>{okText}</button>}
        {onNoPress != null && <button onClick={onNoPress}>{noText}</button>}
      </div>
    </div>
  );
}

export default AlertComponent;
