import React, { useEffect, useState } from "react";
import screenfull from "screenfull";

import "./style.css";

const TopBar = ({
  onMenuPress,
  canGoPrevious,
  canGoNext,
  getPreviousQuestion,
  getNextQuestion,
  onNotePress,
  onLabValuesPress,
}) => {
  const [isFullscreen, setFullScreen] = useState(screenfull.isFullscreen);

  useEffect(() => {
    if (screenfull && screenfull.isEnabled) {
      screenfull.on("change", toggle);
    }
    function toggle() {
      setFullScreen(screenfull.isFullscreen);
    }

    return () => screenfull.isEnabled && screenfull.off("change", toggle);
  }, []);

  return (
    <div className="top-bar">
      <div className="item">
        <button onClick={onMenuPress}>
          <img style={{ width: 25 }} src="./assets/icons/menu.png" alt="menu" />
        </button>
      </div>
      <div style={{ flexGrow: 1 }} className="buttons">
        <button disabled={!canGoPrevious} onClick={getPreviousQuestion}>
          <img alt="" src="/assets/icons/left-arrow.png" />
          <span>Previous</span>
        </button>

        <button disabled={!canGoNext} onClick={getNextQuestion}>
          <img alt="" src="/assets/icons/right-arrow.png" />
          <span>Next</span>
        </button>
      </div>
      <div className="buttons">
        <button className="_hidden" onClick={() => screenfull.toggle()}>
          <img
            alt=""
            src={
              isFullscreen
                ? "/assets/icons/fullscreen-exit.png"
                : "/assets/icons/fullscreen.png"
            }
          />
          <span>{isFullscreen ? "Exit Fullscreen" : "Fullscreen"}</span>
        </button>
        <button className="_hidden">
          <img alt="" src="/assets/icons/tutorial.png" />
          <span>Tutorial</span>
        </button>
        <button>
          <img src="./assets/icons/flag.png" alt="flag" />
          <span>Flag</span>
        </button>
        <button onClick={onLabValuesPress}>
          <img alt="" src="/assets/icons/labs.png" />
          <span>Lab Values</span>
        </button>
        <button onClick={onNotePress}>
          <img alt="" src="/assets/icons/edit.png" />
          <span>Note</span>
        </button>
      </div>
    </div>
  );
};

export default React.memo(TopBar);
