import React, { useCallback, useEffect, useState } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import TopBar from "../components/topBar";
import QuestionList from "../components/questionList";
import NoteComponent from "../components/note";
import FeedbackComponent from "../components/feedback";
import Modal from "../components/modal";
import "./style.css";
import AnswerSheet from "../components/answerSheet";
import BottomBar from "../components/bottomBar";
import { questions as Q } from "../store/model";
import actions from "../actions";
import AlertComponent from "../components/modal/alert";
import SettingsComponent from "../components/settings";
import LabValues from "../components/labValues";

function App({
  setQuestions,
  questions,
  currentQuestionIndex,
  setCurrentQuestionIndex,
  numberOfQuestions,
  canGoPrevious,
  canGoNext,
  getPreviousQuestion,
  getNextQuestion,
}) {
  const isNotMobile = document.body.clientWidth > 768;

  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [UIState, setUIState] = useState({
    feedbackVisible: false,
    notesVisible: false,
    labValuesVisible: false,
    settingsVisible: false,
    modalVisible: false,
  });

  const [showQuestions, setShowQuestions] = useState(isNotMobile);

  useEffect(() => {
    setQuestions(Q);
  }, [setQuestions]);

  useEffect(() => {
    if (currentQuestionIndex > -1) {
      setCurrentQuestion(questions[currentQuestionIndex]);
    }
  }, [currentQuestionIndex, questions]);

  const onGoNext = useCallback(() => getNextQuestion(), [getNextQuestion]);
  const onGoPrevious = useCallback(() => getPreviousQuestion(), [
    getPreviousQuestion,
  ]);

  const questionPress = useCallback((index) => setCurrentQuestionIndex(index), [
    setCurrentQuestionIndex,
  ]);

  const toggleModal = () =>
    setUIState({ ...UIState, modalVisible: !UIState.modalVisible });
  const toggleFeedback = () =>
    setUIState({
      ...UIState,
      feedbackVisible: !UIState.feedbackVisible,
    });

  const toggleNote = () =>
    setUIState({ ...UIState, notesVisible: !UIState.notesVisible });

  const toggleSetting = () =>
    setUIState({ ...UIState, settingsVisible: !UIState.settingsVisible });

  const toggleLab = () =>
    setUIState({ ...UIState, labValuesVisible: !UIState.labValuesVisible });

  const toggleQuestions = () => setShowQuestions(!showQuestions);

  return (
    <section id="app">
      <QuestionList
        visible={showQuestions}
        currentQuestionNumber={currentQuestionIndex + 1}
        totalQuestions={numberOfQuestions}
        onQuestionPress={questionPress}
        currentIndex={currentQuestionIndex}
        questions={questions}
      />
      <section id="content">
        <TopBar
          canGoPrevious={canGoPrevious}
          canGoNext={canGoNext}
          getPreviousQuestion={onGoPrevious}
          getNextQuestion={onGoNext}
          onNotePress={toggleNote}
          onLabValuesPress={toggleLab}
          onMenuPress={toggleQuestions}
        />
        {UIState.labValuesVisible && <LabValues close={toggleLab} />}
        {currentQuestion != null && <AnswerSheet question={currentQuestion} />}
        <NoteComponent
          draggable={isNotMobile}
          close={toggleNote}
          isVisible={UIState.notesVisible}
        />
        {UIState.settingsVisible && <SettingsComponent close={toggleSetting} />}

        {UIState.feedbackVisible && (
          <FeedbackComponent draggable={isNotMobile} close={toggleFeedback} />
        )}
        <BottomBar
          onFeedbackPress={toggleFeedback}
          onAlert={toggleModal}
          onSettingPress={toggleSetting}
        />
      </section>

      {UIState.modalVisible && (
        <Modal>
          <AlertComponent
            onNoPress={toggleModal}
            okayPress={toggleModal}
            title="Confirm Action"
            content="Before you can begin to determine what the composition of a particular paragraph will be, you must first decide on an argument and a working thesis statement for your paper."
          />
        </Modal>
      )}
    </section>
  );
}
const mapStatToProps = (state) => ({
  ...state.questions,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      setQuestions: actions.setQuestions,
      setCurrentQuestionIndex: actions.setCurrentQuestionIndex,
      getNextQuestion: actions.getNextQuestion,
      getPreviousQuestion: actions.getPreviousQuestion,
    },
    dispatch
  ),
});

export default connect(mapStatToProps, mapDispatchToProps)(App);
