import React from "react";

function SettingsComponent({ close }) {
  return (
    <div className="_setting-container">
      <button onClick={close} className="_closeBtn">
        &times;
      </button>
      <h3>Settings</h3>
      <div className="_setting-section">
        <h4>Appearance</h4>
        <div className="_setting-options">
          <ul>
            <li className="_setting-item">
              <p>Font Size</p>
              <div style={{ display: "flex" }}>
                <button>-</button>
                <button>+</button>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div className="_setting-section">
        <h4>Features</h4>
        <div className="_setting-options">
          <ul>
            <li className="_setting-item">
              <p>Confirm Answer Omission</p>
              <input type="checkbox" />
            </li>
            <li className="_setting-item">
              <p>Show Timer</p>
              <input type="checkbox" />
            </li>
            <li className="_setting-item">
              <p>Split Screen Explanations</p>
              <input type="checkbox" />
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default SettingsComponent;
