import React, { useCallback, useState } from "react";
import Draggable from "react-draggable";

function NoteComponent({ isVisible, close, draggable }) {
  const [note, setNote] = useState("");

  const onTextChange = useCallback(({ target: { value } }) => {
    setNote(value);
  }, []);

  return (
    isVisible && (
      <Draggable disabled={!draggable} axis="both" handle=".handle">
        <div style={{ width: 380 }} className="note handle slideIn">
          <div className="header">
            <h3>Manage Notes</h3>
            <button onClick={close} className="closeBtn">
              &times;
            </button>
          </div>
          <div className="text-area">
            <textarea value={note} onChange={onTextChange} rows="15"></textarea>
          </div>
          <div className="footer">
            <button onClick={close}>Save &amp; Close</button>
            <button onClick={() => setNote("")}>Clear Notes</button>
          </div>
        </div>
      </Draggable>
    )
  );
}

export default NoteComponent;
