import { Types } from "../actions/questionActions";
import { createReducer } from "reduxsauce";

const INITIAL_STATE = {
  questions: [],
  currentQuestionIndex: -1,
  numberOfQuestions: 0,
  canGoPrevious: false,
  canGoNext: false,
};

const setQuestions = (_, { questions }) => ({
  questions,
  numberOfQuestions: questions.length,
  currentQuestionIndex: 0,
  canGoPrevious: false,
  canGoNext: questions.length > 0,
});

const setCurrentQuestionIndex = (state, { index }) => ({
  ...state,
  currentQuestionIndex: index,
  canGoNext: index < state.numberOfQuestions - 1,
  canGoPrevious: index > 0,
});

const goNext = (state) => {
  const currentQuestionIndex = state.currentQuestionIndex + 1,
    canGoPrevious = true,
    canGoNext = currentQuestionIndex < state.numberOfQuestions - 1;

  return { ...state, currentQuestionIndex, canGoNext, canGoPrevious };
};

const goPrevious = (state) => {
  const currentQuestionIndex = state.currentQuestionIndex - 1,
    canGoPrevious = currentQuestionIndex > 0,
    canGoNext = true;

  return { ...state, currentQuestionIndex, canGoNext, canGoPrevious };
};

const resetQuestions = () => INITIAL_STATE;

const actionHandlers = {
  [Types.SET_QUESTIONS]: setQuestions,
  [Types.GET_NEXT_QUESTION]: goNext,
  [Types.GET_PREVIOUS_QUESTION]: goPrevious,
  [Types.SET_CURRENT_QUESTION_INDEX]: setCurrentQuestionIndex,
  [Types.RESET_QUESTIONS]: resetQuestions,
};

export default createReducer(INITIAL_STATE, actionHandlers);
