import React from "react";
import Draggable from "react-draggable";
function FeedbackComponent({ close, draggable }) {
  return (
    <Draggable disabled={!draggable} axis="both" handle=".handle">
      <div className="note handle slideIn">
        <div className="header">
          <h3>Feedback</h3>
          <button onClick={close} className="closeBtn">
            &times;
          </button>
        </div>
        <div className="text-area">
          <textarea
            placeholder={
              'We appreciate your feedback. However, please provide as much detail as possible in the permitted space avoiding simplistic feedback like "Good question", "hard question" etc.'
            }
            rows="18"
          ></textarea>
        </div>
        <div className="footer">
          <div style={{ display: "flex", alignItems: "flex-start" }}>
            <input type="checkbox" name="tech_issue" id="tech_issue" />
            <label
              htmlFor="tech_issue"
              style={{ fontSize: "small", marginLeft: 8 }}
            >
              Check here if concern is for software/technical issue
            </label>
          </div>
          <button>Submit</button>
        </div>
      </div>
    </Draggable>
  );
}

export default FeedbackComponent;
