import React from "react";
function LabValues({ close }) {
  return (
    <div className="_lab-container slideIn">
      <div style={{ padding: 20, textAlign: "center", position: "relative" }}>
        <h3 style={{ fontWeight: "bold" }}>Lab Values</h3>
        <button
          onClick={close}
          style={{
            position: "absolute",
            top: 10,
            right: 10,
            color: "#ffffff",
            backgroundColor: "#164d80",
            border: "none",
            height: 25,
            width: 25,
            display: "flex",
            alignItems: "center",
            borderRadius: 25,
            justifyContent: "center",
          }}
        >
          &times;
        </button>
      </div>
      <table>
        <thead>
          <tr>
            <th>Blood, Serem, Plasma</th>
            <th>Reference Range</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alkaline phosphatase, serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
          <tr>
            <td>Alanine aminotransferase (ALT), serum</td>
            <td>8-40 U/L</td>
          </tr>
        </tbody>
      </table>
      <p style={{ margin: 16 }}>
        For the last 2, the TagsList should be better alongside with a series of
        improvements for Desktop as well (better UI for multiple tags, hide tags
        when selected, better multi-selection, etc). As for the Dashboard and
        Alert page headers, the Query one recently had an update to wrap both
        tags and control buttons, if you think it's worth it I can update them.
        The dashboard one currently hides a lot of things on the mobile version
        as the way to solve it.
      </p>
    </div>
  );
}

export default LabValues;
