import React, { useEffect, useState } from "react";
import moment from "moment";
const thenTime = moment().add(1, "hours");

const BottomBar = ({ onFeedbackPress, onAlert, onSettingPress }) => {
  const [countDown, setCountDown] = useState(null);

  useEffect(() => {
    const counterInterval = setInterval(() => {
      let c = thenTime.diff(moment()),
        d = moment(c);

      setCountDown({
        h: d.format("HH"),
        m: d.format("mm"),
        s: d.format("ss"),
      });
    }, 1000);

    return () => {
      clearInterval(counterInterval);
    };
  }, []);

  return (
    <div className="top-bar">
      <div className="item mobile-hidden">
        <p className="bold">Current Test Info</p>
        <span>Frank Test | React Demo Exam</span>
      </div>
      <div style={{ flexGrow: 1, marginLeft: 12, marginRight: 12 }}>
        <p>Duration: 1:00:00</p>
        <h5
          style={{ textTransform: "uppercase", marginBottom: 4, marginTop: 2 }}
          className="bold"
        >
          Time remaining
        </h5>
        {countDown && (
          <span
            style={{
              backgroundColor: "red",
              padding: 2,
              display: "inline-block",
            }}
            className="bold"
          >
            {`${countDown.h}:${countDown.m}:${countDown.s}`}
          </span>
        )}
      </div>
      <button onClick={onSettingPress}>
        <img alt="" src="/assets/icons/settings.png" />
        <span>Settings</span>
      </button>
      <button onClick={onFeedbackPress}>
        <img alt="" src="/assets/icons/feedback.png" />
        <span>Feedback</span>
      </button>
      <button onClick={onAlert}>
        <img alt="" src="/assets/icons/pause.png" />
        <span>Suspend</span>
      </button>
      <button onClick={onAlert}>
        <img alt="" src="/assets/icons/exit.png" />
        <span>Exit</span>
      </button>
    </div>
  );
};

export default React.memo(BottomBar);
