import React from "react";
import "./styles.css";

const QuestionsList = ({
  questions,
  onQuestionPress,
  currentIndex,
  currentQuestionNumber,
  totalQuestions,
  visible,
}) => {
  const mapQuestions = questions.map((question, index) => (
    <li
      className={currentIndex === index ? "active" : null}
      onClick={() => onQuestionPress(index)}
      key={question.id}
    >
      Q{question.id}
    </li>
  ));
  return (
    <div className={`sidebar scroll-container ${visible && "active"}`}>
      <div style={{ padding: 12, textAlign: "center" }}>
        <p className="bold">
          Question: {currentQuestionNumber} of {totalQuestions}
        </p>
        <span>QID: 47593</span>
      </div>
      <ol>{mapQuestions}</ol>
    </div>
  );
};

export default React.memo(QuestionsList);
