import React from "react";
import "./styles.css";

const AnswerSheet = ({ question }) => {
  const renderAnswers = question.answers.map(({ key, answer }) => (
    <li key={key}>
      <span>{key}.</span>
      <input id={`answer-${key}`} value={key} name="answer" type="radio" />
      <label htmlFor={`answer-${key}`}>{answer}</label>
    </li>
  ));
  return (
    <div className="answer-sheet">
      <div className="container">
        <h2>Question</h2>
        <p>{question.question}</p>
      </div>
      <div style={{ marginTop: 32 }} className="container">
        <h2>Answers</h2>
        <ul>{renderAnswers}</ul>
      </div>
      <br />
      <br />
    </div>
  );
};

export default AnswerSheet;
